import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';

// Layout
import DashboardLayout from './components/DashboardLayout';

// Pages
import LoginPage from './pages/LoginPage';
import DashboardPage from './pages/DashboardPage';
import PlacementOverview from './pages/PlacementOverview';
import PlacementStatistics from './pages/PlacementStatistics';
import DepartmentsPage from './pages/DepartmentsPage';
import ITDepartmentPage from './pages/departments/ITDepartmentPage';
import Alumni2025 from './pages/departments/it/alumni/Alumni2025';
import Alumni2024 from './pages/departments/it/alumni/Alumni2024';
import Alumni2023 from './pages/departments/it/alumni/Alumni2023';
import AlumniYearPage from './pages/departments/it/alumni/AlumniYearPage';

function App() {
  return (
    <Router>
      <Routes>
        {/* Login Page */}
        <Route path="/" element={<LoginPage />} />

        {/* Dashboard Main Page */}
        <Route
          path="/dashboard"
          element={
            <DashboardLayout>
              <DashboardPage />
            </DashboardLayout>
          }
        />

        {/* Placement Officer Overview */}
        <Route
          path="/dashboard/placement-officer"
          element={
            <DashboardLayout>
              <PlacementOverview />
            </DashboardLayout>
          }
        />

        {/* Placement Statistics */}
        <Route
          path="/dashboard/placement/statistics"
          element={
            <DashboardLayout>
              <PlacementStatistics />
            </DashboardLayout>
          }
        />

        {/* Departments Page */}
        <Route
          path="/dashboard/departments"
          element={
            <DashboardLayout>
              <DepartmentsPage />
            </DashboardLayout>
          }
        />

        {/* Specific Department Page - IT */}
        <Route
          path="/departments/it"
          element={
            <DashboardLayout>
              <ITDepartmentPage />
            </DashboardLayout>
          }
        />

        {/* Alumni Pages */}
        <Route path="/departments/it/alumni/2025" element={<Alumni2025 />} />
        <Route path="/departments/it/alumni/2024" element={<Alumni2024 />} />
        <Route path="/departments/it/alumni/2023" element={<Alumni2023 />} />

        {/* Dynamic Alumni Year Page */}
        <Route path="/departments/it/alumni/:year" element={<AlumniYearPage />} />

        {/* Fallback Route */}
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
    </Router>
  );
}

export default App;
